<template>
  <section>
    <v-container fluid>
      <v-row>
        <v-col md="12">
          <PageHeader :title="headercontent.title" />
        </v-col>
        <v-col md="8">
          <v-form
            ref="form"
            v-model="valid"
            lazy-validation
            class="form-section"
          >
            <v-row>
              <v-col cols="6">
                <v-text-field
                  v-model="title"
                  outlined
                  label="Subcategory title"
                  class="mr-btm-minus"
                ></v-text-field>
              </v-col>
              <v-col cols="6">
                <v-select
                  v-model="categoryHandle"
                  :items="allCategory.results"
                  label="Category"
                  item-text="name"
                  item-value="connectedTo"
                  outlined
                ></v-select>
              </v-col>
           
              <v-col cols="6">
                <v-select
                  v-model="status"
                  :items="statuses"
                  label="Status"
                  outlined
                  item-text="name"
                  item-value="status"
                ></v-select>
              </v-col>

             
              <v-col cols="12" class="submitbtn">
                <v-btn
                  :disabled="!valid"
                  class="submitbutton btn-dark"
                  @click="validate"
                  v-if="!allLoadingCurd"
                >
                  Add {{ headercontent.title }}
                </v-btn>
                <v-progress-circular
                  v-if="allLoadingCurd"
                  indeterminate
                ></v-progress-circular>
              </v-col>
            </v-row>
          </v-form>
        </v-col>
      </v-row>
    </v-container>
  </section>
</template>

<script>
import PageHeader from "@/components/dashboard/PageHeader";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "AddSubemotion",
  components: { PageHeader },
  data: () => ({
    valid: true,
    items: ["Foo", "Bar", "Fizz", "Buzz"],
    msg: "Vue Image Upload and Resize Demo",
    hasImage: false,
    image: null,
    title: "",
    description: "",
    categoryHandle: "",
    sort: 0,
    headercontent: {
      title: "Add Subemotion",
      addlink: "/dashboard/subemotion/add",
      downloadlink: true,
    },
    content: "<h2>I am Example</h2>",
    editorOption: {
      // Some Quill options...
    },
    titleRules: [(v) => !!v || "Category Title is required"],
    statusRules: [(v) => !!v || "Status is required"],
    sortRules: {
      required: (value) => !!value || "Sort is required.",
      nan: (v) => !isNaN(v) || "Sort should be number",
    },
    descriptionRules: [(v) => !!v || "Description is required"],
    statuses: [
      { name: "Yes", status: true },
      { name: "No", status: false },
    ],
    status: true,
    imageUrl: null,
  }),
  methods: {
    removeImage() {
      this.imageUrl = "";
    },
    validate() {
      if (this.$refs.form.validate()) {
        let fd = new FormData();
        fd.append("title", this.title);
        fd.append("description", this.description);
        fd.append("sort", this.sort);
        fd.append("status", this.status);
        fd.append("categoryHandle", this.categoryHandle);

        if (this.$route.params.slug == undefined) {
          if (this.image != null) {
            fd.append("image", this.image);
            fd.append("fileType", "images");
            fd.append("imageFile", "categories");
            this.addSubcategory(fd);
          } else {
            this.addSubcategory(fd);
          }
        } else {
          if (this.image != null) {
            fd.append("id", this.id);
            fd.append("image", this.image);
            fd.append("fileType", "images");
            fd.append("imageFile", "categories");
            this.updateSubcategory(fd);
          } else {
            if (this.imageUrl == null) {
              fd.append("id", this.id);
              this.updateSubcategory(fd);
            } else {
              console.log(this.imageUrl);
              fd.append("id", this.id);
              fd.append("imageUrl", this.imageUrl);
              this.updateSubcategory(fd);
            }
          }
        }
      }
    },
    setImage: function(output) {
      this.hasImage = true;
      this.image = output;
      console.log("Info", output.info);
      console.log("Exif", output.exif);
    },
    onEditorBlur(quill) {
      console.log("editor blur!", quill);
    },
    onEditorFocus(quill) {
      console.log("editor focus!", quill);
    },
    onEditorReady(quill) {
      console.log("editor ready!", quill);
    },
    onEditorChange({ quill, html, text }) {
      console.log("editor change!", quill, html, text);
      this.content = html;
    },
    ...mapActions([
      "fetchCategory",
      "addSubcategory",
      "fetchSubCategoryBySlug",
    ]),
  },
  computed: {
    ...mapGetters(["allLoadingCurd", "allSubcategory", "allCategory"]),
    editor() {
      return this.$refs.myQuillEditor.quill;
    },
  },

  mounted() {},

  async created() {
    this.fetchCategory();
    await this.fetchSubCategoryBySlug(this.$route.params.slug);
    if (this.$route.params.slug != undefined) {
      this.title = this.allSubcategory.title;
      this.categoryHandle = this.allSubcategory.categoryHandle;
      this.description = this.allSubcategory.description;
      this.sort = this.allSubcategory.sort;
      this.status = this.allSubcategory.status;
      this.imageUrl = this.allSubcategory.imageUrl;
      this.id = this.allSubcategory._id;
    }
  },
};
</script>

